* {
  margin: 0;
  padding: 0;
}
/* Scrollbar-effect------- */
.accordian::-webkit-scrollbar {
  width: 5px;
  height: 8px;
}
.accordian::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #e4e4e4;
}
.accordian::-webkit-scrollbar-thumb {
  background: #0089ff;
  border-radius: 10px;
  transition: 0.5s;
}
.accordian::-webkit-scrollbar-thumb:hover {
  background: #d5b14c;
  transition: 0.5s;
}

/* --------- */

.accordian {
  background: #fff;
  width: 250px;
  padding: 10px;
  float: left;
  height: 100vh;
  overflow-x: hidden;
  user-select: none;
}

.accordian a {
}

.accordian i {
  margin-right: 10px;
}

.accordian li {
  list-style-type: none;
}

.accordian ul li a {
  color: #9c9c9c;
  text-decoration: none;
  font-size: 15px;
  display: block;
  /* 	line-height: 34px; */
  padding: 12px 15px;
  transition: all 0.15s;
  position: relative;
  border-radius: 3px;
}

.accordian > ul.show-dropdown > li.active > a,
.accordian > ul > li > ul.show-dropdown > li.active > a,
.accordian > ul > li > ul > li > ul.show-dropdown > li.active > a,
.accordian > ul > li > ul > li > ul > li > ul.show-dropdown > li.active > a,
.accordian
  > ul
  > li
  > ul
  > li
  > ul
  > li
  > ul
  > li
  > ul.show-dropdown
  > li.active
  > a,
.accordian
  > ul
  > li
  > ul
  > li
  > ul
  > li
  > ul
  > li
  > ul
  > li
  > ul.show-dropdown
  > li.active
  > a {
  background-color: #a8d4fb;
  color: #0089ff;
  box-shadow: 0px 1px 2px rgba(0, 137, 255, 0.2);
}

.accordian > ul > li > ul,
.accordian > ul > li > ul > li > ul,
.accordian > ul > li > ul > li > ul > li > ul,
.accordian > ul > li > ul > li > ul > li > ul > li > ul,
.accordian > ul > li > ul > li > ul > li > ul > li > ul > li > ul {
  display: none;
}

.accordian > ul > li.active > ul.show-dropdown,
.accordian > ul > li > ul > li.active > ul.show-dropdown,
.accordian > ul > li > ul > li > ul > li.active > ul.show-dropdown,
.accordian > ul > li > ul > li > ul > li > ul > li.active > ul.show-dropdown,
.accordian
  > ul
  > li
  > ul
  > li
  > ul
  > li
  > ul
  > li
  > ul
  > li.active
  > ul.show-dropdown {
  display: block;
}

.accordian > ul > li > ul,
.accordian > ul > li > ul > li > ul,
.accordian > ul > li > ul > li > ul > li > ul,
.accordian > ul > li > ul > li > ul > li > ul > li > ul,
.accordian > ul > li > ul > li > ul > li > ul > li > ul > li > ul {
  padding-left: 20px;
}

